<template>
    <div id="home">
        <nav-bar class="home-nav"><div slot="center">购物街</div> </nav-bar>
        <tab-control :titles="['流行','新款','精选']"
                     @tabClick="tabClick" ref="tabControl1"
                     class="tab-control" v-show="isTabFixed"
        />
        <scroll class="content" ref="scroll"
                :probe-type="3" @scroll="contentScroll"
               :pull-up-load="true" @pullingUp="loadMore">
        <home-swiper :banners="banners" @swiperImageLoad="swiperImageLoad"/>
        <recommend-view :recommends="recommends"/>
        <feature-view/>
        <tab-control :titles="['流行','新款','精选']"
                     @tabClick="tabClick" ref="tabControl2"
                    />
        <good-list :goods="showGoods"/>
        </scroll>
        <back-top @click.native="backClick" v-show="isShowBackTop"/>
<!--        1.给vue组件绑定事件时候，必须加上native ，否则会认为监听的是来自Item组件自定义的事件-->
<!--        2.等同于在子组件中：  子组件内部处理click事件然后向外发送click事件：$emit("click".fn)-->
    </div>
</template>

<script>
    import HomeSwiper from './childComps/HomeSwiper'
    import RecommendView from './childComps/RecommendView'
    import FeatureView from './childComps/FeatureView'

    import NavBar from 'components/common/navbar/NavBar'
    import TabControl from 'components/content/tabControl/TabControl'
    import GoodList from 'components/content/goods/GoodsList'
    import Scroll from 'components/common/scroll/Scroll'
    import backTop from 'components/content/backTop/backTop'


    import { getHomeMultidata, getHomeGoods } from "network/home"
    import {itemListenerMixin} from "common/mixin";

    export default {
        name: "Home",
        components: {
            HomeSwiper,
            RecommendView,
            FeatureView,

            NavBar,
            TabControl,
            GoodList,
            Scroll,
            backTop
        },mixins:[
            itemListenerMixin
        ],
        data() {
            return {
                banners: [],
                recommends: [],
                goods: {
                    'pop': {page: 0, list: []},
                    'new': {page: 0, list: []},
                    'sell': {page: 0, list: []},
                },
                currentType: 'pop',
                isShowBackTop: false,
                tabOffsetTop:0,
                isTabFixed:false,
                saveY:0
            }
        },
        computed: {
            showGoods() {
                return this.goods[this.currentType].list
            }
        },
        destroyed(){
            },
        activated(){//进入页面时载入Y轴位置
            this.$refs.scroll.scrollTo(0,this.saveY)
            this.$refs.scroll.refresh()
        },
        deactivated(){//离开页面时记载Y轴位置
            this.saveY=this.$refs.scroll.getScrollY()

            // 取消全局事件 的监听
            this.$bus.$off('itemImgLoad',this.itemImgListener)
        },

        created() {
            // 1.请求多个数据
            this.getHomeMultidata()

            // 2.请求商品数据
            this.getHomeGoods('pop')
            this.getHomeGoods('new')
            this.getHomeGoods('sell')


        },
        mounted(){

        },

        methods: {
            /**
             * 事件监听相关的方法
             */

            tabClick(index) {
                switch (index) {
                    case 0:
                        this.currentType = 'pop'
                        break
                    case 1:
                        this.currentType = 'new'
                        break
                    case 2:
                        this.currentType = 'sell'
                        break
                }
                this.$refs.tabControl1.currentIndex=index;
                this.$refs.tabControl2.currentIndex=index;
            },
            backClick(){
                this.$refs.scroll.scrollTo(0,0)
            },
            contentScroll(position){
                // 1.判断BackTop是否显示
               this.isShowBackTop=-position.y>1000
                // 2.决定tabControl是否吸顶（position：fixed)
                this.isTabFixed=(-position.y)>this.tabOffsetTop
            },
            loadMore(){//加载更多
                this.getHomeGoods(this.currentType)
                this.$refs.scroll.scroll.refresh()//加载完图片刷新scroll计数
            },
            swiperImageLoad(){
                this.tabOffsetTop =this.$refs.tabControl2.$el.offsetTop;
                // console.log(this.$refs.tabControl2.$el.offsetTop);
            },
            /**
             * 网络请求相关的方法
             */
            getHomeMultidata() {
                getHomeMultidata().then(res => {
                    // this.result = res;
                    this.banners = res.data.banner.list;
                    this.recommends = res.data.recommend.list;
                })
            },
            getHomeGoods(type) {
                const page = this.goods[type].page + 1
                getHomeGoods(type, page).then(res => {
                    this.goods[type].list.push(...res.data.list)
                    this.goods[type].page += 1

                    this.$refs.scroll.finishPullUp()  //上拉获取更多
                })
            }
        }
    }
</script>

<style scoped>
    #home{
        /*padding-top: 44px;*/
        height: 100vh;/*相对于视口的高度。视口被均分为100单位的vh*/
        position: relative;
    }
.home-nav{
  background-color: var(--color-tint);
  color: #fff;

    /*position: fixed;*/
    /*left: 0;*/
    /*right: 0;*/
    /*top: 0;*/
    /*z-index: 9;*/

}

    .content{
        overflow: hidden;
        position: absolute;
        top:44px;
        bottom: 49px;
        left: 0;
        right: 0;
    }
.tab-control{
    position: relative;
    z-index: 9;
}

</style>